import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'

import './index.scss'
import logo from '../../images/logo.svg'

import OpenInNewIcon from '../social-icon/open-in-new-icon/index'

const Title = ({ title }) =>
  <Link to={'/'}>
    <img  className={'header-title'} src={logo} alt={title} height="40"/> {/* Hay que poner la altura en el scss para que no se rompa la pgina en mviles */}
  </Link>

const Header = ({ title = '', children }) => (
  <div className='header-container'>
    <Title
      title={title}
      location={'/'}
    />

    <div>
      {children}
      <a href="https://whackahack.com/foro/threads/islas-del-viento-demo-4-abril-2020.61705/" className={`tab-item`} target="_blank">Desarrollo <OpenInNewIcon /></a>
    </div>
  </div >
)

Title.propTypes = {
  title: PropTypes.string,
}

Header.propTypes = {
  title: PropTypes.string,
}

export default Header
